.containerDadosSaude {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5em;
  margin-bottom: 5em;
}

@media screen and (max-width: 920px) {
  .containerDadosSaude h1 {
    text-align: center;
    font-size: 20px;
  }
  .containerDadosSaude h3 {
    font-size: 15px;
  }
  .containerDadosSaude li {
    font-size: 15px;
  }
}

.containerDadosSaude h3 {
  color: rgb(66, 79, 255);
  margin-bottom: 1em;
}

.containerDadosSaude h1 {
  margin-bottom: 1.5em;
}

.containerDadosSaude li {
  margin-bottom: 0.8em;
}

.containerImgDadosSaude {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3em;
  margin-top: 3em;
}

@media screen and (max-width: 920px) {
  .containerImgDadosSaude {
    display: flex;
    flex-direction: column;
    columns: 2;
  }
}

.cardDadosSaude {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0.5em;
  width: 80%;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.cardDadosSaude img {
  height: 20em;
  margin-top: 1em;
}

.cardDadosSaude legend {
  display: flex;
  text-align: center;
  margin-top: 1em;
  margin-bottom: 0.5em;
  width: 65%;
}
